<script>
	import { renderable, tileSize, colors } from './game.js';

	let color = '#000000';
	export let baseline = 'middle';

	export let pos;

	export let fontSize = 16;
	export let fontFamily = '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica';

	renderable({
		render: props => {
			const { context, width, height } = props;
			if (pos.name) {

				context.font = `${fontSize}px ${fontFamily}`;
				context.textBaseline = baseline;

				let textWidth = context.measureText(pos.name).width;

				context.fillStyle = $colors.rooms;
				context.fillRect(pos.x * $tileSize + $tileSize/2 - textWidth/2 - 2, pos.y * $tileSize + $tileSize - 4 - 2, textWidth + 4, fontSize + 4)

				context.fillStyle = $colors.background + "FF";
				context.fillText(pos.name, pos.x * $tileSize + $tileSize/2 - textWidth/2, pos.y * $tileSize + $tileSize - 4);
			}
		}, height: 6
	});
</script>

<!-- The following allows this component to nest children -->
<slot></slot>
