export { readfile, copyToClipboard };
import { game } from './stores.js';

function readfile(binfile) {
  let reader = new FileReader(binfile);
  reader.onload = function(evt) {
      let dungeon = JSON.parse(reader.result);

      let minX = 0;
      let minY = 0;

      let hover = {x: 0, y: 0, w: 1, h: 1};

      for(let i in dungeon.rects) {
        let room = dungeon.rects[i];

        if(room.x < minX) minX = room.x
        if(room.y < minY) minY = room.y
      }

      minX -= 1;
      minY -= 1;

      for(let i in dungeon.rects) {
        let room = dungeon.rects[i];

        room.x -= minX;
        room.y -= minY;

        room.visible = false;
      }

      for(let i in dungeon.doors) {
        let room = dungeon.doors[i];

        room.x -= minX;
        room.y -= minY;
      }

      dungeon.init = true;

      game.update(g => {
        g.dungeon = dungeon;
        g.dungeon.figures = [];
        return g;
      });
  }

  reader.readAsText(binfile);
}

function copyToClipboard(text) {
  var dummy = document.createElement("textarea");
  document.body.appendChild(dummy);
  dummy.value = text;
  dummy.select();
  document.execCommand("copy");
  document.body.removeChild(dummy);
}
