<script>
	import { renderable, width, height, tileSize } from './game.js';
  import { mouse } from './mouse.js';

	renderable({
		render: (props, dt) => {
			const { context, width, height } = props;
			context.save();

			if($mouse.pick) {
        let x = Math.round($mouse.tileX * 2 - 1) * $tileSize / 2;
        let y = Math.round($mouse.tileY * 2 - 1) * $tileSize / 2;
        let w = $tileSize - 8;
        let h = $tileSize - 8;

        context.beginPath();
        context.fillStyle = $mouse.pick.color + "AA";
        context.ellipse(x + $tileSize/2, y + $tileSize/2, w/2, h/2, 0, 0, Math.PI * 2);
        context.closePath();
        context.fill();
        context.beginPath();
      }

			context.restore();
		}, height: 8
	});
</script>
