<script>
	import { onMount, onDestroy, setContext } from 'svelte';
	import { createEventDispatcher } from 'svelte';
	import { mouse } from './mouse.js'
	import { game } from '../stores.js'
	import { key, width, height, canvas as canvasStore, context as contextStore, 	pixelRatio, props, time, tileSize, cam, } from './game.js';
	import { sendMoveFigure } from '../WebHandler.js';

	export let killLoopOnError = true;
	export let attributes = {};

	let clickStart;
	let listeners = [];
	let canvas;
	let context;
	let frame;

	onMount(() => {
		// prepare canvas stores
		context = canvas.getContext('2d', attributes);
		canvasStore.set(canvas);
		contextStore.set(context);

		// start game loop
		return createLoop((elapsed, dt) => {
			time.set(elapsed);
			render(dt);
		});
	});

	setContext(key, {
		add (fn) {
			this.remove(fn);
			listeners.push(fn);

			if (fn.setup) {
				let p = fn.setup($props);
				if (p && p.then) p;
			}
			fn.ready = true;

			listeners.sort((e1, e2) => e1.height - e2.height);
		},
		remove (fn) {
			const idx = listeners.indexOf(fn);
			if (idx >= 0) listeners.splice(idx, 1);
		}
	});

	function render (dt) {
		context.save();

		context.translate(-$cam.dx, -$cam.dy);
		context.scale($pixelRatio, $pixelRatio);
		listeners.forEach(entity => {
			try {
				if (entity.mounted && entity.ready && entity.render) {
					entity.render($props, dt);
				}
			} catch (err) {
				console.error(err);
				if (killLoopOnError) {
					cancelAnimationFrame(frame);
					console.warn('Animation loop stopped due to an error');
				}
			}
		});
		context.restore();
	}

	function handleResize () {
		width.set(window.innerWidth);
		height.set(window.innerHeight);
		pixelRatio.set(window.devicePixelRatio);
	}

	function createLoop (fn) {
		let elapsed = 0;
		let lastTime = performance.now();
		(function loop() {
			frame = requestAnimationFrame(loop);
			const beginTime = performance.now();
			const dt = (beginTime - lastTime) / 1000;
			lastTime = beginTime;
			elapsed += dt;
			fn(elapsed, dt);
		})();
		return () => {
			cancelAnimationFrame(frame);
		};
	}

	const dispatch = createEventDispatcher();
	function handleMouseUp(ev) {
		$mouse.down = false;

		let timeDis = Date.now() - clickStart;
		let data = {
			x: $mouse.worldX,
			y: $mouse.worldY,
			tileX: $mouse.tileX,
			tileY: $mouse.tileY
		}

		let newPos = false;
		if($mouse.pick) {
			let x = $mouse.pick.x;
			let y = $mouse.pick.y;

			let newX = Math.round($mouse.tileX * 2) / 2 - 0.5;
			let newY = Math.round($mouse.tileY * 2) / 2 - 0.5;
			newPos = !(x === newX && $mouse.pick.y === newY);

			if(newPos) sendMoveFigure($mouse.pick, { x: newX, y: newY }, $game.id, $game.thisPlayer.id);
			$mouse.pick = false;
		}

		if(timeDis < 200 && !newPos && ev.button === 0) dispatch('click', data);
	}

	function handleMouseMove(event) {
		if($mouse.enter) {
			if($mouse.down ) {
				if(!$mouse.pick) {
					$cam = {
						dx: $cam.dx + ($mouse.screenX - event.x) * $pixelRatio,
						dy: $cam.dy + ($mouse.screenY - event.y) * $pixelRatio
					}
				}
			}

			$mouse.screenX = event.x;
			$mouse.screenY = event.y;
			$mouse.worldX = event.x + $cam.dx/$pixelRatio;
			$mouse.worldY = event.y + $cam.dy/$pixelRatio;
			$mouse.tileX = $mouse.worldX / $tileSize;
			$mouse.tileY = $mouse.worldY / $tileSize;
		}
	}

	function handleMouseDown(event) {
		$mouse.down = true;
		clickStart = Date.now();
		if(event.button === 0) dispatch('down', event);
	}
</script>

<canvas
	on:mousemove={handleMouseMove}
	on:mousedown={handleMouseDown}
	on:mouseup={handleMouseUp}
	on:mouseenter={$mouse.enter=true}
	on:mouseleave={$mouse.enter=false}
	bind:this={canvas}
	width={$width * $pixelRatio}
	height={$height * $pixelRatio}
	style="width: {$width}px; height: {$height}px;"
/>
<svelte:window on:resize|passive={handleResize} />
<slot></slot>

