//DEPLOY
const host = "vast-cliffs-53607.herokuapp.com";

//DEV /*
//const host  = "localhost";
//const port = 3000;

let socketConnection;

function sendRequest(data) {
  return new Promise(function (resolve, reject) {
    let xmlhttp = new XMLHttpRequest();

      xmlhttp.onload = function () {
        var status = xmlhttp.status;
        if (status == 200) {
            resolve(JSON.parse(xmlhttp.responseText));
        } else {
          reject(status);
        }
      };

      xmlhttp.onerror = function(er) {
        resolve({ isError: true, error: "The Server seems to be offline." });
      }

      //DEPLOY
      xmlhttp.open("POST", `https://${host}`, true);

     //DEV
     //xmlhttp.open("POST", `http://${host}:${port}`, true);

     xmlhttp.setRequestHeader("Content-type", "application/json");
     xmlhttp.send(data);
  })
}

function createGame(gameName, masterName) {
  let data = {
    action: "createGame",
    gameName: gameName,
    masterName: masterName
  }

  return sendRequest(JSON.stringify(data));
}

function joinGame(gameID, playerName) {
  let data = {
    action: "joinGame",
    gameID: gameID,
    playerName: playerName
  }

  return sendRequest(JSON.stringify(data));
}

import { get } from 'svelte/store';
import { game, addPlayer, showRoom, showDoor, removePlayer, hideRoom, hideDoor, resetGame, addFigure, moveFigure, removeFigure } from './stores.js';
import { setError } from './errorstore.js';

function openSocket(gameID, playerID) {
  //DEPLOY
  socketConnection = new WebSocket(`wss://${host}/websocket`);

  //DEV
  //socketConnection = new WebSocket(`ws://${host}:8080`);

  socketConnection.onopen = function () {
    let data = {action: "register", pid: playerID, gid: gameID};
    socketConnection.send(JSON.stringify(data));
  };

  socketConnection.onmessage = function (e) {
    let data = JSON.parse(e.data);

    if(data.isError) {
      setError(data.message);
    }

    if(data.action != "ping") console.log(data);
    if(data.action === "userJoined") {
      addPlayer(data.user);
    } else if(data.action === "showRoom") {
      showRoom(data.room);
    } else if(data.action === "showDoor") {
      showDoor(data.door)
    } else if(data.action === "kick") {
      if(socketConnection) socketConnection.close();
      resetGame();
      setError("You were kicked: " + data.message);
    } else if(data.action === "ping") {
      let gameID = get(game).id;
      let playerID = get(game).thisPlayer.id;
      let data = {action: "pong", pid: playerID, gid: gameID};
      socketConnection.send(JSON.stringify(data));
    } else if(data.action === "userLeft") {
      removePlayer(data.user);
    } else if(data.action === "hideDoor") {
      hideDoor(data.door);
    } else if(data.action === "hideRoom") {
      hideRoom(data.room)
    } else if(data.action === "addFigure") {
      addFigure(data.figure);
    } else if(data.action === "moveFigure") {
      moveFigure(data.figure, data.to);
    } else if(data.action === "removeFigure") {
      removeFigure(data.figure);
    }
  };
}

function sendShowRoom(room, gid, pid) {
  let data = { action: "showRoom", room: room, pid: pid, gid: gid }
  let dataStr = JSON.stringify(data);
  if(socketConnection) socketConnection.send(dataStr);
}

