<script>
	import { renderable, width, height, tileSize, colors } from './game.js';
	import Text from './Text.svelte';

	export let pos;

	renderable({
		render: (props, dt) => {
			const { context, width, height } = props;
			context.save();

			let x = pos.x * $tileSize;
			let y = pos.y * $tileSize;
			let w = $tileSize - 8;
			let h = $tileSize - 8;

			context.beginPath();
			context.fillStyle = pos.color;
			context.ellipse(x + $tileSize/2, y + $tileSize/2, w/2, h/2, 0, 0, Math.PI * 2);
			context.closePath();
			context.fill();
			context.beginPath();

			context.restore();
		}, height: 7
	});
</script>

<Text
	fontSize={16}
	baseline='top'
	bind:pos={pos}
/>
