<script>
	import { fly } from 'svelte/transition';
	import { createGame, openSocket, joinGame } from './WebHandler.js';
	import { game } from './stores.js';
	import { setError, resetError } from './errorstore.js';
	import Sources from './Sources.svelte'

	const queryString = window.location.search;
	const urlParams = new URLSearchParams(queryString);

	var params = { params1:"val1", params2:"val2" };
	urlParams.set("test", 4);

	let gameName = urlParams.get("gid");
	let playerName = "";
	let loading = 0;

	async function clickCreate() {
		loading = 1;
		let answer = await createGame(gameName, playerName);
		loading = 0;

		if(!answer.isError) {
			$game = {
				id: answer.game.id,
				name: answer.game.name,
				playerNames: [
					{ name: playerName, isMaster: true, isYou: true }
				],
				thisPlayer: {
					id: answer.player.id,
					name: playerName,
					isMaster: true,
					isYou: true
				},
				dungeon: answer.dungeon,
			};

			openSocket($game.id, $game.thisPlayer.id);
			resetError();
		} else {
			setError(answer.error);
		}
	}

	async function clickJoin() {
		loading = 2;
		let answer = await joinGame(gameName, playerName);
		loading = 0;

		if(!answer.isError) {
			$game = {
				id: answer.id,
				name: answer.gameName,
				playerNames: answer.player,
				thisPlayer: {
					id: answer.playerID,
					name: playerName,
					isMaster: false,
					isYou: true
				},
				dungeon: answer.dungeon
			};

			openSocket($game.id, $game.thisPlayer.id);
			resetError();
		} else {
			setError(answer.error);
		}
}
</script>

<div class="main">
	<div class="card">
		<div class="row">
			<div class="col">
		  <input placeholder="Enter Game Name" class="is-rounded" bind:value={gameName} disabled={loading} />
			</div>
		</div>
		<div class="row">
			<div class="col">
				<input placeholder="Enter your Name" class="is-rounded" bind:value={playerName} disabled={loading} />
			</div>

			<div class="col-2">
				<button class="button is-full-width" on:click={clickJoin} disabled={loading}>
						<span> Join </span> <span class="hide-sm"> &nbsp;Game </span>
						{#if loading == 2}
							<div transition:fly="{{ x: -15, duration: 100 }}"><div class="ld ld-ring ld-spin"></div></div>
						{/if}
				</button>

			</div>

			<div class="col-2">
				<button class="button primary is-full-width" on:click={clickCreate} disabled={loading}>
					<span> Create </span> <span class="hide-sm"> &nbsp;Game </span>
					{#if loading == 1}
						<div transition:fly="{{ x: -15, duration: 100 }}"><div class="ld ld-ring ld-spin"></div></div>
					{/if}
				</button>
			</div>
		</div>
	</div>
	<Sources />
</div>

<div class="version"> <span>v1.1</span></div>

<style>
.main {
	width: 75%;
	margin-top: 64px;
	margin-left: auto;
  margin-right: auto;
}

