	background: var(--bg-secondary-color);
	margin-bottom: 200px;
}

.button {
	display: flex;
	justify-content: center;
	align-content: center;
}

.button div {
	margin-left: 8px;
}

.version {
	position: fixed;
  bottom: 4px;
  right: 16px;
	color: var(--color-lightGrey);
}
</style>
