<script>
	import Menu from './Menu.svelte';
	import MenuOption from './MenuOption.svelte';
	import MenuDivider from './MenuDivider.svelte';
	import { tick } from 'svelte';
	import { mouse } from '../visuals/mouse.js';
	import { game } from '../stores.js';
	import { cam } from '../visuals/game.js';
	import { sendCreateFigure, sendDeleteFigure } from '../WebHandler.js';
	import Modal from './Modal.svelte';

	let pos = { x: 0, y: 0 };
	let figName = "";

	let showMenu = false;
	let showModal = false;

	async function onRightClick(e) {
		if (showMenu) {
			showMenu = false;
			await new Promise(res => setTimeout(res, 100));
		}

		let x = Math.round($mouse.tileX * 2) / 2 - 0.5;
    let y = Math.round($mouse.tileY * 2) / 2 - 0.5;

		pos = { x: $mouse.screenX, y: $mouse.screenY, tileX: x, tileY: y };

		let fig = $game.dungeon.figures.find(f => f.x === x && f.y === y);
		if(fig) figName = fig.name? fig.name: "Unnamed figure";
		else figName = "";

		showMenu = true;
	}

	function closeMenu() {
		showMenu = false;
	}

	function clickAddFigure() {
		showModal = true;
	}

	function addFigure(ev) {
		let data = ev.detail;
		let fig = {
			x: pos.tileX,
			y: pos.tileY,
			name: data.name,
			color: data.color
		}

		sendCreateFigure(fig, $game.id, $game.thisPlayer.id);
	}

	function deleteFigure() {
		let fig = {
			x: pos.tileX,
			y: pos.tileY
		}
		sendDeleteFigure(fig, $game.id, $game.thisPlayer.id);
	}

	function resetCamera() {
		$cam.dx = 0;
		$cam.dy = 0;
	}
</script>

{#if showMenu}
	<Menu {...pos} on:click={closeMenu} on:clickoutside={closeMenu}>
		{#if figName.length > 0 && $game.thisPlayer.isMaster}
			<MenuOption
				on:click={deleteFigure}
				text={"Delete " + figName} />
		{:else if $game.thisPlayer.isMaster}
			<MenuOption
				on:click={clickAddFigure}
				text="Add Figure" />
		{/if}
		<MenuOption
			on:click={resetCamera}
			text="Reset Camera" />
	</Menu>
{/if}

<Modal bind:show={showModal} on:accept={addFigure} />

<svelte:body on:contextmenu|preventDefault={onRightClick} />
