import { writable } from 'svelte/store';

export { game, addPlayer, showRoom, showDoor, removePlayer, hideDoor, hideRoom, resetGame, addFigure, moveFigure, removeFigure };

const game = writable({id: -1});

//> PLAYERS

function addPlayer(playerName) {
  game.update(g => {
    g.playerNames.push(playerName);
    return g;
  });
}

function removePlayer(user) {
  game.update(g => {
    let playerIndex = g.playerNames.findIndex(u => u.name === user.name);
    if(playerIndex > 0) g.playerNames.splice(playerIndex, 1);
    return g;
  });
}

//> ROOMS

function showRoom(room) {
  game.update(g => {
    if(g.thisPlayer.isMaster) {
      let r = g.dungeon.rects.find(r => r.x == room.x && r.y == room.y && r.w == room.w && r.h == room.h);
      r.visible = room.visible;
    } else {
      g.dungeon.rects = [...g.dungeon.rects, room];
    }
    return g;
  });
}

function hideRoom(room) {
  game.update(g => {
    if(g.thisPlayer.isMaster) {
      let r = g.dungeon.rects.find(r => r.x == room.x && r.y == room.y && r.w == room.w && r.h == room.h);
      r.visible = room.visible;
    } else {
      let roomIndex = g.dungeon.rects.findIndex(r => r.x == room.x && r.y == room.y && r.w == room.w && r.h == room.h);
      if(roomIndex >= 0) g.dungeon.rects.splice(roomIndex, 1);
    }
    return g;
  });
}

//> DOORS

function showDoor(room) {
  game.update(g => {
    if(!g.thisPlayer.isMaster) {
      g.dungeon.doors = [...g.dungeon.doors, room];
    }
    return g;
  });
}

function hideDoor(door) {
  game.update(g => {
    if(!g.thisPlayer.isMaster) {
      let doorIndex = g.dungeon.doors.findIndex(e=>e.x === door.x && e.y === door.y);
      if(doorIndex >= 0) g.dungeon.doors.splice(doorIndex, 1);

    }
    return g;
  });
}

//> FIGURE

function addFigure(fig) {
  game.update(g => {
    g.dungeon.figures.push(fig);
    return g;
  })
}

function moveFigure(fig, to) {
  game.update(g => {
    let f = g.dungeon.figures.find(e => e.x === fig.x && e.y === fig.y);
    f.x = to.x;
    f.y = to.y;
    return g;
  })
}

function removeFigure(fig) {
  game.update(g => {
    let figureIndex = g.dungeon.figures.findIndex(e => e.x === fig.x && e.y === fig.y);
    if(figureIndex >= 0) g.dungeon.figures.splice(figureIndex, 1);
    return g;
  })
}

function resetGame() {
  game.set({ id: -1 });
}
