<script>
  import { game } from './stores.js';
  import { copyToClipboard, readfile } from './util.js'

  let files;
  $: {
      if (files && files[0]) {
          let binfile = files[0];
          readfile(binfile);
      }
  }
</script>

<div class="info">
  <div class="card">
    <button class="button icon-only" on:click={ copyToClipboard("https://phoenixofforce.github.io/?gid=" + $game.id) }>
      <img alt="Copy Game Link" title="Copy Game Link" src="https://icongr.am/feather/copy.svg?size=16">
    </button>
    <b> { $game.name } </b><br>

      {#each $game.playerNames as player}
        <span class:text-primary={ player.isYou }> { player.name.substring(0, 20)  } { player.isMaster? "(Master)": ""} </span><br>
      {/each}

  </div>
  {#if $game.thisPlayer.isMaster && !$game.dungeon.init}
    <input class="button primary" type="file" bind:files />
  {/if}
</div>

<style>
.info {
  position: fixed;
  top: 0;
  left: 0;
  max-width: 25%;
  z-index: 5;
}
</style>
